<template>
  <v-chart class="chart" :option="option" :autoresize="true"/>
</template>

<script setup>

import VChart, {THEME_KEY} from "vue-echarts";
import {getCurrentInstance, onMounted, provide, ref} from "vue";
import * as echarts from 'echarts/core';
import {
  DataZoomComponent,
  GridComponent,
  MarkLineComponent,
  TitleComponent,
  TooltipComponent
} from 'echarts/components';
import {LineChart} from 'echarts/charts';
import {UniversalTransition} from 'echarts/features';
import {CanvasRenderer} from 'echarts/renderers';
import GpxProcess from "@/components/Control/gpxProcess";


provide(THEME_KEY, "light")

echarts.use([
  MarkLineComponent,
  DataZoomComponent,
  TitleComponent,
  TooltipComponent,
  GridComponent,
  LineChart,
  CanvasRenderer,
  UniversalTransition
]);

function round(number, precision) {
  let temp = Math.pow(10, precision)
  return Math.round(number * temp) / temp;
}

let dataLength = 105;
let data = Array(dataLength);

let date = new Date(2022, 5, 16, 5, 30, 30);
for (let i = 0; i < dataLength; i++) {
  date = new Date(date.valueOf() + 1000)
  //
  // data[i] = {
  //   value: [date.valueOf(), round(Math.random() * 20 + 100, 2)],
  //   someValue: '哈哈哈哈哈'
  // }
  data[i] = {value: [+date, round(Math.random() * 20 + 100, 2)]}
}

let _option = {
  title: {text: 'title', left: 'middle'},
  // grid: {bottom: 30},
  tooltip: {
    trigger: 'axis',
    formatter: (params) => {
      params = params[0].data;
      // console.log(params.value[1])
      const dt = new Date(params.value[0])

      let year = dt.getFullYear();//年
      let month = dt.getMonth() + 1;//月；从0开始所以要加1
      let date = dt.getDate();//日
      let hour = dt.getHours();
      let minutes = dt.getMinutes();
      let seconds = dt.getSeconds();

      month = month > 10 ? month : "0" + month;
      date = date > 10 ? date : "0" + date;
      hour = hour > 10 ? hour : "0" + hour;
      minutes = minutes > 10 ? minutes : "0" + minutes;

      return [year, month, date].join('-') + ' ' + [hour, minutes, seconds].join(':');
    },
    axisPointer: {
      type: 'cross',
      animation: true
    }
  },
  xAxis: {
    type: 'time',
    splitLine: {
      show: false
    }, boundaryGap: false
  },
  yAxis: {
    type: 'value',
    scale: true,
    boundaryGap: [0, '100%'],
    splitLine: {
      show: false
    }
  },
  toolbox: {
    show: true,
    feature: {
      saveAsImage: {}
    }
  },
  dataZoom: [
    {
      type: 'slider'
    }
  ],
  series: [
    {
      type: 'line',
      smooth: true,
      showSymbol: false,
      data: data,
      markLine: {
        data: [
          {
            name: 'Y 轴值为 100 的水平线',
            xAxis: data[50].value[0]
          },
        ]
      }
    }
  ]
}
let setIntervalID;

let option = ref(_option)


const ctx = getCurrentInstance().appContext.config.globalProperties;

ctx.$bus.$on("fileRead", (file) => {
  const fr = new FileReader();
  fr.readAsText(file, 'utf-8')
  fr.onload = function () {
    if (setIntervalID !== undefined) {
      clearInterval(setIntervalID);
    }
    let gp = new GpxProcess(fr.result);
    let oldOption = option.value.series;

    oldOption[0].data = gp.chartData;
    option.value.series = oldOption;

    let i = 0;
    let dataLength = gp.chartData.length;
    setIntervalID = setInterval(() => {
      if (i < dataLength) {
        option.value.series[0].markLine = {text: 'look at me now', data: [{xAxis: gp.chartData[i].value[0]}]};
        i = i + 1;
      } else {
        clearInterval(setIntervalID);
      }
    }, 1000);

  }
});

ctx.$bus.$on("titleRead", (newOptions) => {
  option.value = newOptions;
});

onMounted(() => {

})

</script>

